// Functional Imports
import { Navigate, useRoutes } from "react-router-dom";
import { useDispatch } from "react-redux";
import { HeaderSliceActions } from "../store/header-slice";
import { useParams } from "react-router-dom";

// Components Imports
import Profile from "../Apps/AccountSetings/Profile/Profile";


function AccountSettingsPages(props){
    const dispatcher = useDispatch();
    const baseURL = '/profile';

    dispatcher( HeaderSliceActions.setHeaders({ 
        currentApp: 'Account Settings', 
        appTabs: [
            { name: 'Profile', endpoint: 'info', baseURL }
        ],
        appPermissions: {}
    }) );

    const routes = useRoutes([
        { index: true, element: <Navigate to='info' relative='path' replace={true} /> },
        { path: ':employeeId', element: <RedirectToProfileView baseURL={baseURL} /> },
        { path: ':employeeId/info', element: <Profile baseURL={baseURL} /> },
        { path: 'info', element: <Profile baseURL={baseURL} /> },    
        { path: '*', element: <Navigate to='/error' state={{ statusText: 'Page Not Found!', status: 404 }} /> }    
    ]);

    return routes;
}

function RedirectToProfileView(props){
    const { employeeId } = useParams();
    return <Navigate to={`${props.baseURL}/${employeeId}/view`} replace={true} />
}

export default AccountSettingsPages;