// Functional imports
import cssNameSimplifier from '../../../utility/css-name-simplifier';
import { useEffect, useState } from 'react';
import { useHttpClientWithNavigate } from '../../../utility/http-hook';
import { HeaderSliceName } from '../../../store/header-slice';
import { useDispatch, useSelector } from 'react-redux';
import { AlertSliceActions } from '../../../store/alert-slice';
import { useParams } from 'react-router-dom';
import isEmpty from '../../../utility/is-empty';

// Components imports
import { Box, Paper, Avatar, Typography, Button, Divider } from '@mui/material';
import { FormControl, InputLabel, OutlinedInput, InputAdornment, IconButton } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';


// Style Imports
import importedStyles from './Profile.module.css';

const STYLES = cssNameSimplifier(importedStyles);

function Profile(props) {
    const [disabled, setDisabledState]  = useState(true);
    const [userData, setUserData]       = useState({});
    const sendRequest                   = useHttpClientWithNavigate();
    const dispatch                      = useDispatch()                   
    const { employeeId }                = useParams();
    const loggedInUserId                = useSelector(state => state[HeaderSliceName]).employee.id;
    const isCurrentLoggedInUser         = (isEmpty(employeeId))? true : loggedInUserId === employeeId;

    useEffect(() => {
        (async () => {
            try{
                const url = props.baseURL + ((isEmpty(employeeId))?  '/info' : `/${employeeId}/info`);
                const method = 'GET';
        
                const data = await sendRequest({ url, method });

                data.avatarName = '';
                const employeeName = data.name.trim().split(' ');
                
                if (employeeName?.length > 1) {
                    data.avatarName = employeeName[0].charAt(0).toUpperCase() + employeeName[1].charAt(0).toUpperCase()
                } else {
                    data.avatarName = employeeName[0].charAt(0).toUpperCase();
                }
                
                setUserData({ ...data, old: data });
            } catch(err) {
                dispatch( AlertSliceActions.showAlert({ message: err.msg || 'Failed to fetch user info', title: 'Request Failed!', severity: 'error' }) );
            }
        })()
    }, [dispatch, employeeId, props.baseURL, sendRequest]);

    function toggleInputDisable(){
        if(!disabled){
            setUserData(prev => ({ ...prev.old, old: prev.old }))
        }

        setDisabledState(prev => !prev);
    }

    function handelValueChange(event){
        const name = event.target.name;
        const value = event.target.value;

        setUserData(prev => ({...prev, [name]: value}));
    }

    async function handelFormSubmit(event){
        try {
            event.preventDefault();

            const url       = props.baseURL + '/profile/update';
            const method    = 'POST';
            const headers   = { 'content-type': 'application/json' };

            const body      = JSON.stringify({ name: userData.name, phone: userData.phone, id: userData.id });

            const response = await sendRequest({ url, method, body, headers });

            dispatch( AlertSliceActions.showAlert({ message: response.msg || 'Updated Successfully', title: 'Request Completed!', severity: 'success' }) );

            setDisabledState(true);
        } catch (err) {
            dispatch( AlertSliceActions.showAlert({ message: err.msg || 'Failed to update user info', title: 'Request Failed!', severity: 'error' }) );
        }
    }

    return (
        <div className={STYLES.container}>
            <Box sx={{ textAlign: "center" }}>
                <Paper elevation={3} sx={{ p: 2 }}>
                    <Avatar sx={{ width: 76, height: 76, margin: 'auto', fontSize: 40 }}>{userData.avatarName}</Avatar>
                    <Typography variant="h4" component="h2" marginTop={2}>
                        {userData.id?.toUpperCase()}
                    </Typography>
                </Paper>
            </Box>

            <form onSubmit={handelFormSubmit}>
                <div className={STYLES.data}> 
                    <FormControl variant="outlined" className={STYLES.inputContainer} >
                        <InputLabel htmlFor='role'>Role</InputLabel>
                        <OutlinedInput
                            id='role'
                            label='role'
                            disabled
                            defaultValue='Role'
                            value={userData.Role?.name}
                        />
                    </FormControl>

                    <FormControl variant="outlined" className={STYLES.inputContainer} >
                        <InputLabel htmlFor='email'>Email</InputLabel>
                        <OutlinedInput
                            id='email'
                            label='Email'
                            disabled
                            defaultValue='Email'
                            value={userData.email}
                        />
                    </FormControl>

                    <FormControl variant="outlined" className={STYLES.inputContainer} required>
                        <InputLabel htmlFor='name'>Full Name</InputLabel>
                        <OutlinedInput
                            id='name'
                            endAdornment={
                                disabled && isCurrentLoggedInUser &&
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle edit option"
                                        edge="end"
                                        onClick={toggleInputDisable}
                                    >
                                        <EditIcon fontSize='small' />
                                    </IconButton>
                                </InputAdornment>
                            }
                            label='Full Name'
                            name='name'
                            defaultValue='Full Name'
                            disabled={disabled}
                            onChange={handelValueChange}
                            required
                            value={userData.name}
                        />
                    </FormControl>


                    <FormControl variant="outlined" className={STYLES.inputContainer} required>
                        <InputLabel htmlFor='phone'>Phone</InputLabel>
                        <OutlinedInput
                            id='phone'
                            endAdornment={
                                disabled && isCurrentLoggedInUser &&
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle edit option"
                                        edge="end"
                                        onClick={toggleInputDisable}
                                    >
                                        <EditIcon fontSize='small' />
                                    </IconButton>
                                </InputAdornment>
                            }
                            label='Phone'
                            name='phone'
                            defaultValue='Phone'
                            disabled={disabled}
                            onChange={handelValueChange}
                            required
                            value={userData.phone}
                        />
                    </FormControl>
                </div>

                {
                    !disabled && isCurrentLoggedInUser &&
                    <>
                        <Divider sx={{ marginTop: 2 }} />

                        <div className={STYLES.data}>
                            <Button variant="outlined" onClick={toggleInputDisable}>Cancel</Button>
                            <Button variant="contained" type="submit">Save</Button>
                        </div>
                    </>
                }
            </form>
        </div>
    )
}

export default Profile;